import axios from "axios";
import React , { useState , useEffect } from "react";
import { useTheme } from "../../Context/ThemeContext";

import style from "./style.module.css";
import JobApplyForm from "../JobApplyForm/page";
import { toast } from "react-toastify";

export default function AvailableJobs({newFilters , search})
{
    const [allJobs, setAllJobs] = useState([]);
    const [apply, setApply] = useState(false);
    const [jobId , setJobId] = useState(null)

    const { checked } = useTheme();
    
    function openJobApply(id)
    {
        setApply(true);
        document.body.style.overflow = 'hidden';
        setJobId(id)
    }

    function closeJobApply()
    {
        setApply(false);
        document.body.style.overflow = 'auto';
        setJobId(null)
    }

    function removeJob(id)
    {
        setAllJobs(allJobs.filter(allJobs => (allJobs._id !== id)))
    }

    useEffect(() => {
        async function getJobs()
        {
            try
            {
                const res = await axios.get("/api/getAvailableJobs" ,
                {
                    headers : {
                        "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"],
                        "filters" : JSON.stringify(newFilters),
                        "search" : search
                    }
                })
                setAllJobs(res.data);
            }
            catch(error)
            {
                toast.error("Something wrong happened");
                console.log(error);
            }
        }
        getJobs();
    },[newFilters , search])

    return <><div className={style.jobs}>
        {allJobs.map((item , index) => {
            return <div key={index}>
                <div className={style.title} style={{color : checked ? "white" : "black"}}>{item.title}</div>
                <div className={style.description} style={{color : checked ? "aquamarine" : "#e22020"}}>{item.description}</div>
                <div className={style.user} style={{color : checked ? "pink" : "#df56e4"}}><span>Posted by</span> : {item.user}</div>
                <div className={style.location} style={{color : checked ? "wheat" : "#9824d1"}}>{item.email}</div>
                <div className={style.container} style={{color : checked ? "yellow" : "#1da159"}}>
                    <div className={style.salary}><span>Salary</span> : {item.salary} &#8377;</div>
                    <div className={style.experience}><span>Past experience</span> : {item.experience} {item.experience > 1 ? 'years' : 'year'}</div>
                </div>
                <div className={style.container} style={{color : checked ? "yellow" : "#1da159"}}>
                    <div className={style.jobType}><span>Job type</span> : {item.jobType}</div>
                    <div className={style.workMode}><span>Work mode</span> : {item.workMode}</div>
                </div>
                <div className={style.location} style={{color : checked ? "wheat" : "#9824d1"}}>{item.location}</div>
                <button className={style.apply} onClick={()=>openJobApply(item._id)}>Apply Now</button>
            </div>
        })}
    </div>
    <JobApplyForm isOpen={apply} onRequestClose={closeJobApply} contentLabel="Example Modal" jobId={jobId} removeJob={removeJob}/>
    </>
}