import style from "./style.module.css";
import CountryCodes from "./CountryCodes";
import React from "react";

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import ReactModal from "react-modal";
ReactModal.setAppElement('*');

export default function JobApplyForm(props)
{
    async function handleSubmit(event)
    {
        event.preventDefault();
        const form = event.target;
        const formData = new FormData(form);
        formData.append('jobId', props.jobId);
        const applyJob = {
            method: 'POST',
            headers: {
                "auth-token" : JSON.parse(window.localStorage.getItem("auth-token"))["authToken"],
            },
            body: formData
        };
        console.log(applyJob)
        try
        {
            props.removeJob(props.jobId);
            const res = await fetch("/api/applyJob" , applyJob);
            toast.success(res.data);
            props.onRequestClose();
        } catch (error)
        {
            console.log(error);
            toast.warn(error.response.data);
        }
    }

    return <>
        <ReactModal
            isOpen={props.isOpen}
            onRequestClose={props.onRequestClose}
            contentLabel={props.contentLabel}
            className={style.modalContentClass}
            overlayClassName={style.modalOverlayClass}
        >
            <form className={style.form} method="POST" action="/api/applyJob" encType="multipart/form-data" onSubmit={handleSubmit} >
                <div className={style.postHead}>Enter Details</div>
                <div className={style.inputContainer}>
                    <label htmlFor="name">Name</label>
                    <input type="text" name="name" id="name" placeholder="Enter name" autoComplete='off' required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="age">Age</label>
                    <input type="number" name="age" id="age" placeholder="Enter age" autoComplete='off' required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="address">Address</label>
                    <input type="text" name="address" id="address" placeholder="Enter permanent address" autoComplete='off' required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="country">Select Country</label>
                    <select id="country" name="country" required>
                        <CountryCodes />
                    </select>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="number">Phone Number</label>
                    <input type="number" name="number" id="number" placeholder="Enter phone number" autoComplete='off' maxLength={10} required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="email">Email</label>
                    <input type="email" name="email" id="email" placeholder="Enter your email" autoComplete='off' required/>
                </div>
                <div className={style.inputContainer}>
                    <label htmlFor="resume">Resume</label>
                    <input type="file" accept="application/pdf" name="resume" id="resume" autoComplete='off' required/>
                </div>
                <div className={style.btnContainer}>
                    <button type='submit'>Apply</button>
                    <button onClick={props.onRequestClose}>Close</button>
                </div>
            </form>
        </ReactModal>
        <ToastContainer theme="colored" autoClose={4000} hideProgressBar={true} />
    </>
}